import { createSlice } from "@reduxjs/toolkit";
import { ItemCardProps} from "../components/sections/ItemCard"; 
const initialState : any   = {
  cart: [],
};

export const cartSlice = createSlice({
  name: "cartSlice",
  initialState,
  reducers: {
    // Actions
    addToCart: (state, action) => {
        
      if (!state.cart.id) {
        state.cart = [...state.cart, action.payload];
      }
    },
    removeFromCart: (state, action) => {
    //   const index = state.items.findIndex(
    //     (wishlistItem : ItemCardProps) => wishlistItem.data._id === action.payload.id
    //   );
    //   let newWishlist = [...state.items];
    //   if (index >= 0) {
    //     // item exist in the basket ... remove it
    //     newWishlist.splice(index, 1);
    //   } else {
    //     console.warn(
    //       `Cant remove the product (id: ${action.payload.id}) as its not in basket`
    //     );
    //   }
    //   state.items = newWishlist;
    },
  },
});

export const { addToCart, removeFromCart } = cartSlice.actions;

export const selectCartItems = (state : any) => state.cart.cart;
export default cartSlice.reducer;
